.user_block {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: calc(100% - 2em);
    padding: 1em;
    box-sizing: border-box;
    border: 1px solid #e1e1e1;
    margin: 1em;
    border-radius: 0.2em;
}

.user_left_block {
    flex-grow: 1;
}

.user_name {
    font-size: 1.2em;
}

.user_status {
    font-size: 0.8em;
    color: #777;
}

.followButton, .unfollowButton {
    width: 120px;
    margin: 0.5em;
    padding: 0.5em;
    background: #355591;
    text-align: center;
    color: #fff;
    cursor: pointer;
}

.avatar {
    background: #333;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    margin: 0.5em;
    margin-right: 1em;
}

.avatar img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

