
.pagination_wrap {
    display: flex;
    justify-content: center;
    align-items: center;
}

.pagination_wrap > span {
    padding: 5px;
    margin: 5px;
    border: 1px solid #e1e1e1;
    box-sizing: border-box;
    width: 30px;
    height: 30px;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.pagination_wrap > span.selectedPage {
    background: #777;
    border-color: #777;
    color: #fff;
}

